.hero {
    height: 500px;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  
  .hero::after {
    content: "";
    display: block;
    opacity: .3;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: gray;
  }
  
  .hero * {
    position: relative;
    z-index: 2;
  }
  
  .hero h1 {
    color: whitesmoke;
    font-family: 'IM Fell English', serif;
    font-size: 70px;
    padding-top: 100px;
    padding-right:600px;
    margin-top: 0;
  }
  
  